<template>
    <div>
        <van-dialog v-model="show" title="" :show-cancel-button="cancel" confirmButtonText="立即更新" cancelButtonText="暂不更新" confirmButtonColor="#3D7EFF" :before-close="beforeClose">
            <div class="cont">
                <img src="../../assets/logo.png" />
                <h2>您当前版本过低，{{!cancel?'请更新后使用':'请尽快更新'}}</h2>
                <dl>
                    <dd>{{sUpdateDescription}}</dd>
                </dl>
            </div>
        </van-dialog>
    </div>
</template>
<script>
export default {
    data(){
        return{
            // 版本号
            appversion: '',
            newVersion: '',
            lastappversion: '',
            show: false,
            sUpdateDescription: '',
            bntText: '',
            dowUrl: '',
            cancel: false
        }
    },
    methods:{
        beforeClose(action, done) {
            const that = this
            if (action === 'confirm') {
                done(false)
                window.requestFileSystem(window.TEMPORARY, 5 * 1024 * 1024, function (fs) {
                    console.log('file system open: ' + fs.name);
                    fs.root.getFile('gpayapp.apk', {create: true, exclusive: false}, function (fileEntry) {
                        download(fileEntry, that.dowUrl, true);
                        that.show = false
                    }, function () {
                    });
                }, function () {});

                function download(fileEntry, uri, readBinaryData) {
                    let fileTransfer = new FileTransfer();
                    let fileURL = fileEntry.toURL();
                    let trustHosts = true;
                    let options = {};
                    //alert(fileURL)
                    fileTransfer.onprogress = function (progress) {
                        // 进度，这里使用文字显示下载百分比
                        let downloadProgress = (progress.loaded / progress.total) * 100;
                        that.$toast("已经下载：" + Math.floor(downloadProgress) + "%");
                        //that.bntText = "已经下载：" + Math.floor(downloadProgress) + "%"
                        /*$ionicLoading.show({
                            template: "已经下载：" + Math.floor(downloadProgress) + "%"
                        });*/
                        // if (downloadProgress > 99) {
                        //     alert("下载完成")
                        // }
                    };
                    fileTransfer.download(uri, fileURL, function (e) {
                        // 打开下载下来的APP
                        //alert(fileURL)
                        cordova.plugins.fileOpener2.open(fileURL, 'application/vnd.android.package-archive');
                        //alert("下载完成1")
                        that.show = false
                    }, function (error) {
                        //alert("下载失败")
                        that.show = false
                    }, trustHosts, options);
                }
            } else {
                done();
            }
        },
    }
}
</script>
<style lang="less" scoped>
    .cont{
        text-align: center;
        img{
            width: 80px;
            height: 80px;
            margin-top: 20px;
        }
        h2{
            font-size: 15px;
            padding-top: 15px;
        }
        dl{
            font-size: 13px;
            padding: 0 25px;
            text-align: left;
            margin-top: 20px;
        }
    }
    .pro{
        text-align: left;
        padding: 10px 30px;
        font-size: 12px;
        padding-bottom: 20px;
    }
</style>