<template>
    <div id="app" class="wap-wrap-box">
        <!-- <loading v-if="isLoading"></loading> -->
        <van-skeleton title :row="srow" :loading="loading">
            <router-view/>
        </van-skeleton>
        <version-comp ref="version"></version-comp>
    </div>
</template>

<script>
    import {mapActions,mapState,mapGetters} from 'vuex'
    // import Loading from "./views/loading"
    import VersionComp from "./views/version"

    import {getAppVersion} from '@/api/udc'
    import { compVersion } from '@/utils/util'
    import store from '@/store'

    export default {
        name: 'App',
        data(){
            return{
                websock:null,
                loading: true,
                srow: document.body.clientHeight / 32,
            }
        },
        computed:{
            ...mapState(["isLoading"]),
        },
        components: {
            // Loading,
            VersionComp
        },
        created(){
            const that = this
            this.GetPlatForm();//获取手机系统版本，Android,iOS
            this.GetVersion().then(() =>{
                const param = {
                        sAppOS: 'android'
                    }
                    console.log(this.version())
                    getAppVersion(param).then((res) => {
                        var lastappversion = res.data.sMinVersion//res.data.sNewVersion;
                        let bForceUpdate = res.data.bForceUpdate
                        let test_v = compVersion(lastappversion,this.version());
                        console.log(lastappversion,'lastappversion')
                        store.commit('GET_BUILDID',res.data.sNewVersion)

                        let sApkUrl = res.data.sApkUrl;//下载地址
                        that.dowUrl = encodeURI(sApkUrl); // 可以从服务端获取更新APP的路径
                        that.sUpdateDescription = res.data.sUpdateDescription//更新内容描述
                        //alert(JSON.stringify(res.data)) test_v
                        if(test_v && bForceUpdate){
                            //alert("强制更新版本");
                            //去做弹窗和下载
                            that.$refs.version.show = true
                        }else{
                            that.$refs.version.show = false
                        }
                        that.$refs.version.dowUrl = encodeURI(sApkUrl);
                        that.$refs.version.newVersion = res.data.sNewVersion;
                    });
            }).catch((err) =>{
                console.log(err,'00000')
            })
            this.QueryCountry();
            this.GetErrorCode();
            this.initWebSocket();
            this.GetUserExten()
        },
        destroyed () {
            if(this.websock){
                this.websock.close() // 离开路由之后断开websocket连接
            }
        },
        methods:{
            ...mapActions(['GetPlatForm','GetVersion','QueryCountry','GetErrorCode','GetUserExten']),
            ...mapGetters(['version']),
            initWebSocket () { // 初始化weosocket
                this.$store.commit('INIT_WEBSOCKET')
            },
        },
        watch:{
            $route: {
                handler:function(val, oldVal){
                    this.$nextTick(function(){  //页面加载完成后执行
                        console.log('this.monitorAPP()')
                        this.loading = false
                    })
                },
                // 深度观察监听
                deep: true
            }
        }
    }
</script>

<style>
    #app {
        font-family: Avenir, Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
        /* height: 100%; */
        /* background-color: #F3F5FD; */
    }
    body{
        width: 100%;
        /* height: 100%; */
    }
    .header_title {
        position: fixed;
        width: 100%;
        left: 0;
        top: 0;
        z-index: 1000;
    }
    .van-nav-bar .van-nav-bar__left .van-icon{
        right: 10px !important;
    }
    input{
        caret-color: #3D7EFF;
    }
    video::-webkit-media-controls-enclosure {
        overflow: hidden;
    }
    
    video::-webkit-media-controls-panel {
        width: calc(100% + 30px);
    }
</style>