import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/'
import VueStorage from 'vue-ls'

import { message, Modal } from 'ant-design-vue'
import VueClipboard from 'vue-clipboard2'
import Vant from 'vant';
import { Toast, Dialog } from 'vant';
import 'vant/lib/index.css';
import '@/assets/style/common.less'
import '@/utils/filterBase'


import './permission' // permission control



Vue.prototype.$message = message
Vue.prototype.$confirm = Modal.confirm
Vue.config.productionTip = false
Vue.config.devtools = true;
Vue.use(VueStorage)
Vue.use(VueClipboard)
Vue.use(Vant);
Vue.use(Toast);
Vue.use(Dialog);


Vue.prototype.$setgoindex = function () {
	if (window.history.length <= 1) {
		if (location.href.indexOf('?') === -1) {
			window.location.href = location.href + '?goindex=true'
		} else if (location.href.indexOf('?') !== -1 && location.href.indexOf('goindex') === -1) {
			window.location.href = location.href + '&goindex=true'
		}
	}
}

let vueInstance = new Vue({
	router,
	store,
	render: h => h(App),
}).$mount('#app')

document.addEventListener('deviceready', function () {

	try {
		setTimeout(function () {
			navigator.splashscreen.hide();
		}, 100);

		cordova.getAppVersion.getVersionNumber(function (version) {
			// alert(version);
		});
		if(window._CONFIG['device']  != 'ios'){
			SMSReceive.startWatch(function() {
				// alert("开始监听短信")
				console.log('smsreceive: watching started');
			}, function() {
				// alert("监听短信失败")
				console.warn('smsreceive: failed to start watching');
			});
		}
		// alert(device.platform);
		screen.orientation.lock('portrait');//锁定方向

		window.networkFlag = 1;// 默认手机联网
		let networkState = navigator.connection.type;
		if (networkState == 'none') {// 无网
			window.networkFlag = 2;
			Toast("当前无网络，请检查网络连接");
		}
		document.addEventListener("offline", function () { // 断网
			window.networkFlag = 2;
			Toast("当前无网络，请检查网络连接");
			// 其它语句
		}, false);
		document.addEventListener("online", function () {  // 开启网络
			window.networkFlag = 1;
			// 其它语句
		}, false);

		let time;
		document.addEventListener("backbutton", function () {
			let intervalTime = new Date() - time;
			if (vueInstance.$route.name.toString() === "index"
				|| vueInstance.$route.name.toString() === "order"
				|| vueInstance.$route.name.toString() === "ads"
				|| vueInstance.$route.name.toString() === "my"
				|| vueInstance.$route.name.toString() === "balance"
				|| vueInstance.$route.name.toString() === "adList"
				|| vueInstance.$route.name.toString() === "robOrder"

			) {
				//登录页面的时候
				if (intervalTime < 1000) {
					navigator.app.exitApp();
				} else {
					//刷新time
					time = new Date();
					Toast("双击退出应用");
				}
			} else if (vueInstance.$route.name.toString() === "my/balance/withdraw/success") {
				vueInstance.$router.back(-1);
			} else if (vueInstance.$route.name.toString() === "3") {
				vueInstance.$router.push('/2');
			} else if (vueInstance.$route.name.toString() === "4") {
				vueInstance.$router.push('/3');
			} else if (vueInstance.$route.name.toString() === "5") {
				vueInstance.$router.push('/4');
			} else if (vueInstance.$route.name.toString() === "6") {
				vueInstance.$router.push('/5');
			} else if (vueInstance.$route.name.toString() === "7") {
				vueInstance.$router.push('/6');
			} else {
				//console.log("this.$route.path.toString():",vueInstance.$route.path.toString());
				//console.log("this.$route.name.toString();:",vueInstance.$route.name.toString());
				//在其他页面时返回上一页面
				vueInstance.$router.back(-1);
			}

			// let intervalTime = new Date() - time;
			// let x = document.getElementById('exit');
			// if (x) {
			//   //两次按键间隔时间小于一秒退出APP
			//   if (intervalTime < 1000) {
			//     navigator.app.exitApp();
			//   } else {
			//     //刷新time
			//     time = new Date();
			//     Toast("双击退出应用");
			//   }
			// } else {
			//
			// }
		}, false);
	} catch (err) {
		//alert(JSON.stringify(err))
	}
}, false);

