import Vue from 'vue'
import {login,queryUserInfo,securityLogin} from "@/api/auth";
import {getUserExten,registerUserExten,getAppVersion,getQueryCountry,getErrorCode,getUserMailUnreadCount} from '@/api/udc'
import {getOrderList,getShareOrderList} from '@/api/otc'
import {Notify} from 'vant';
import router from '@/router'
import dayjs from 'dayjs'
import store from '@/store'

const user = {
    state: {
        isLogin:false,
        userInfo: {},
        stUserExtenInfo:{},
        userCountry: '',
        errorMsg:'',
        orderCount:0,
        orderState: false,
        mailUnreadCount:0,
        listenSmsStatus:false
    },
    mutations: {
        SET_LOGIN_STATUS: (state) => {
            state.isLogin = true
            //登录成功后，执行websocket登录
            if(!store.getters.isWsLogin) store.commit('LOGIN_WS')
        },
        SET_LOGOUT_STATUS: (state) => {
            state.isLogin = false
        },
        SET_USER_INFO: (state, info) => {
            state.userInfo = info
        },
        SET_USER_EXTEN_INFO: (state, info) => {
            state.stUserExtenInfo = info
        },
        CLEAR_USER_INFO: (state) => {
            state.userInfo = {}
        },
        CLEAR_USER_EXTEN_INFO: (state) => {
            state.stUserExtenInfo = {}
        },
        SET_IS_EMAIL_VERIFY:(state,bool) => {
            state.userInfo.bSwitchEmailVerify = bool
        },
        SET_IS_CELLPHONE_VERIFY:(state,bool) => {
            state.userInfo.bSwitchCellphoneVerify = bool
        },
        SET_IS_GOOGLE_VERIFY:(state,bool) => {
            state.userInfo.bSwitchGoogleVerify = bool
        },
        SET_USER_CODE:(state,country) => {
            state.userCountry = country
        },
        SET_USER_ERROE:(state,msg) => {
            state.errorMsg = msg
        },
        SET_ORDER_COUNT:(state,count) => {
            state.orderCount = count
        },
        SET_MAIL_UNREAD_COUNT:(state,count) => {
            state.mailUnreadCount = count
        },
        SET_ORDER_STATE:(state,st) => {
            state.orderState = st
        },
        SET_LISTENSMS_STATE:(state,bool) => {
            state.listenSmsStatus = bool
        },
    },
    actions: {
        Login:({commit},loginParams)=>{
            return new Promise((resolve, reject) => {
                //先获取APP信息
                const param = {
                    sAppOS:'android'
                }
                getAppVersion(param).then((res)=>{
                    login(loginParams)
                        .then((res)=>{
                            let keyString = decodeURIComponent(escape(window.atob(res.data.sSessionKey.split('.')[1])))
                            let key = JSON.parse(keyString)
                            Vue.ls.set('SSK',res.data.sSessionKey,key.exp*1000 - dayjs().valueOf())
                            commit('SET_LOGIN_STATUS')
                            resolve(res)
                        }).catch((err) =>{
                            reject(err.data)
                        })
                });
            })
        },
        //安全登录，开启谷歌验证码后需要通过这个登录
        SafeLogin:({commit},loginParams)=>{
            return new Promise((resolve, reject) => {
                securityLogin(loginParams)
                    .then((res)=>{
                        let keyString = decodeURIComponent(escape(window.atob(res.data.sSessionKey.split('.')[1])))
                        let key = JSON.parse(keyString)
                        // debugger
                        Vue.ls.set('SSK',res.data.sSessionKey,key.exp*1000 - dayjs().valueOf())
                        commit('SET_LOGIN_STATUS')
                        // commit('SET_MFA',key.mfa)
                        resolve(res)
                    }).catch((err) =>{
                        reject(err.data)
                    })
            })
        },
        Logout:({commit})=>{
            commit('CLEAR_USER_INFO')
            commit('CLEAR_USER_EXTEN_INFO')
            commit('SET_LOGOUT_STATUS')
            Vue.ls.clear()
            commit('RECONNECT_WS')
            router.replace('/')
        },
        GetUserInfo:({commit})=>{
            queryUserInfo({iIdle:0})
                .then(res=>{
                    commit('SET_USER_INFO',res.data.stAuth)
                    commit('SET_LOGIN_STATUS')
                    Vue.ls.set('stAuth',res.data.stAuth)
                })
        },
        GetUserExten:({commit,dispatch},payload )=>{
            return new Promise((resolve,reject)=>{
                getUserExten({iIdle:0})
                    .then(res=>{
                        // debugger
                        if(res.iRspCode === 0){

                            // if(res.data.stUserExtenInfo.iUserType !== 1){
                            //     Notify({ type: 'warning', duration: 1000, message: '您已注册用户端！请在用户端登录' });
                            //     dispatch('Logout')
                            //     return
                            // }
                            if(res.data.stUserExtenInfo.iUserType === 1){
                                Notify({ type: 'warning', duration: 1000, message: '您已注册商户端！请在商户端登录' });
                                //dispatch('Logout')
                                return
                            }
                            if(payload && payload.isLogin){
                                Notify({ type: 'success', duration: 500, message: '登录成功！' });
                                //router.push('/otc')
                            }
                            commit('SET_USER_EXTEN_INFO',res.data.stUserExtenInfo)
                            Vue.ls.set('stUserExtenInfo',res.data.stUserExtenInfo)
                            dispatch('GetUserInfo')
                            // 订单数量
                            const params={
                                iOrderType:0,
                                iOrderProcess:1,
                                sToken:'USDT',
                                iPageNo:0,
                                iPageSize:10,
                                iRoleType:0
                            }
                            dispatch('GetOrderCountFun',params)
                            resolve(res.data.stUserExtenInfo)
                        }
                        if(res.iRspCode === -1106){
                            registerUserExten({iUserType:payload.userType})
                                .then(()=>{
                                    // debugger
                                    dispatch('GetUserExten',payload)
                                })
                            resolve()
                        }
                    }).catch((err) =>{
                        reject(err.data)
                    })
            })
        },
        QueryCountry:({commit})=>{
            // 是否有值
            const count = Vue.ls.get('stCountry')
            if(count){
                commit('SET_USER_CODE',count)
            }else{
                getQueryCountry({iIdle:0})
                    .then(res=>{
                        let ch = [], qtArr = [],_arr='',count=res.data.vCountryInfo;
                        for(let code of count){
                            if(code.iCode == 86){
                                ch.push(code)
                            }else{
                                qtArr.push(code)
                            }
                        }
                        _arr = ch.concat(qtArr)
                        commit('SET_USER_CODE',_arr)
                        Vue.ls.set('stCountry',_arr)
                    })
            }
        },
        GetErrorCode:({commit})=>{
            getErrorCode()
            .then(res=>{
                commit('SET_USER_ERROE',res.data.vList)
            })
        },
        GetMailUnreadCount:({commit})=>{
            getUserMailUnreadCount({iIdle:0})
                .then(res=>{
                    commit('SET_MAIL_UNREAD_COUNT',res.data ? res.data.iCount : 0)
                })
        },
        GetOrderCountFun:({commit},param)=>{
            getShareOrderList(param)
            .then(res=>{
                store.commit('SET_ORDER_COUNT',res.data.iTotalCount)
            })
        },
    }
}

export default user
