import Vue from 'vue'
import { postAction } from './index'
const baseUrl = process.env.VUE_APP_AUTH_API_URL
const authUrl = {
    CheckRegistered:'/v1/auth/CheckRegistered',             //注册验证
    SendSMSVerifyCode:'/v1/auth/SendSMSVerifyCode',         //发送短信验证码
    UserRegister:'/v1/auth/UserRegister',                   //用户注册
    UserLogin:'/v1/auth/UserLogin',                         //用户登录
    RefreshSessionKey:'/v1/auth/RefreshSessionKey',         //刷新SessionKey
    UnifySecurityVerify:'/v1/auth/UnifySecurityVerify',     //统一安全认证
    SecurityLogin:'/v1/auth/SecurityLogin',                 //安全登录
    QueryUserInfo:'/v1/auth/QueryUserInfo',                 //查询用户信息
    QueryLoginHistory:'/v1/auth/QueryLoginHistory',         //查询登录历史
    SwitchLoginVerify:'/v1/auth/SwitchLoginVerify',         //开启/关闭登录验证（更新请求字段值，更新接口名称，原：ActionSecurityLogin）
    // UnifySecurityVerify:'/v1/auth/UnifySecurityVerify',  //统一安全认证
    GetVerifyMethod:'/v1/auth/GetVerifyMethod',             //获取验证方式
    ChangePassword:'/v1/auth/ChangePassword',               //修改登录密码
    RetrievePassword:'/v1/auth/RetrievePassword',           //找回登录密码
    ResetPassword:'/v1/auth/ResetPassword',                 //重置登录密码
    CellphoneVerify:'/v1/auth/CellphoneVerify',             //手机验证
    SwitchCellphoneVerify:'/v1/auth/SwitchCellphoneVerify', //开启/关闭手机验证
    CheckVerifyCode:'/v1/auth/CheckVerifyCode',             //检查验证码（更新请求/返回字段）
    EmailVerify:'/v1/auth/EmailVerify',                     //邮箱验证
    SetAntiFishingCode:'/v1/auth/SetAntiFishingCode',       //设置防钓鱼码（更新接口名称，原：SetingAntiFishingCode）
    SendEmailVerifyCode:'/v1/auth/SendEmailVerifyCode',     //发送邮箱验证码
    GetGoogleSecretKey:'/v1/auth/GetGoogleSecretKey',       //获取谷歌密钥
    GoogleVerify:'/v1/auth/GoogleVerify',                   //谷歌验证
    SwitchGoogleVerify:'/v1/auth/SwitchGoogleVerify',       //开启/关闭谷歌验证
    VerifyPassword:'/v1/auth/VerifyPassword',               //验证登录密码
    SwitchEmailVerify:'/v1/auth/SwitchEmailVerify',         //开启/关闭邮箱验证
    RetrieveVerify:'/v1/auth/RetrieveVerify',               //找回验证
}

for(let k in authUrl) {
    authUrl[k] = baseUrl + authUrl[k]
}

const getSMSVerifyCode = (params)=>postAction(authUrl.SendSMSVerifyCode,params);                //获取手机验证码
const checkRegistered = (params)=>postAction(authUrl.CheckRegistered,params);                   //注册验证
const userRegister = (params)=>postAction(authUrl.UserRegister,params);                         //用户注册
const login = (params)=>postAction(authUrl.UserLogin,params);                                   //用户登录
const refreshSessionKey = (params)=>postAction(authUrl.RefreshSessionKey,params);               //刷新SessionKey
const unifySecurityVerify = (params)=>postAction(authUrl.UnifySecurityVerify,params);           //统一安全认证
const securityLogin = (params)=>postAction(authUrl.SecurityLogin,params);                       //安全登录
const queryUserInfo = (params)=>postAction(authUrl.QueryUserInfo,params);                       //查询用户信息
const queryLoginHistory = (params)=>postAction(authUrl.QueryLoginHistory,params);               //查询登录历史
const switchLoginVerify = (params)=>postAction(authUrl.SwitchLoginVerify,params);               //开启/关闭登录验证（更新请求字段值，更新接口名称，原：ActionSecurityLogin）
const getVerifyMethod = (params)=>postAction(authUrl.GetVerifyMethod,params);                   //获取验证方式
const changePassword = (params)=>postAction(authUrl.ChangePassword,params);                     //修改登录密码
const retrievePassword = (params)=>postAction(authUrl.RetrievePassword,params);                 //找回登录密码
const resetPassword = (params)=>postAction(authUrl.ResetPassword,params);                       //重置登录密码
const cellphoneVerify = (params)=>postAction(authUrl.CellphoneVerify,params);                   //手机验证
const switchCellphoneVerify = (params)=>postAction(authUrl.SwitchCellphoneVerify,params);       //开启/关闭手机验证
const checkVerifyCode = (params)=>postAction(authUrl.CheckVerifyCode,params);                   //检查验证码（更新请求/返回字段）
const emailVerify = (params)=>postAction(authUrl.EmailVerify,params);                           //邮箱验证
const setAntiFishingCode = (params)=>postAction(authUrl.SetAntiFishingCode,params);             //设置防钓鱼码（更新接口名称，原：SetingAntiFishingCode）
const sendEmailVerifyCode = (params)=>postAction(authUrl.SendEmailVerifyCode,params);           //发送邮箱验证码
const getGoogleSecretKey = (params)=>postAction(authUrl.GetGoogleSecretKey,params);             //获取谷歌密钥
const googleVerify = (params)=>postAction(authUrl.GoogleVerify,params);                         //谷歌验证
const switchGoogleVerify = (params)=>postAction(authUrl.SwitchGoogleVerify,params);             //开启/关闭谷歌验证
const verifyPassword = (params)=>postAction(authUrl.VerifyPassword,params);                     //验证登录密码
const switchEmailVerify = (params)=>postAction(authUrl.SwitchEmailVerify,params);               //开启/关闭邮箱验证
const retrieveVerify = (params)=>postAction(authUrl.RetrieveVerify,params);                     //找回验证


export {
    authUrl,
    getSMSVerifyCode,
    checkRegistered,
    userRegister,
    login,
    refreshSessionKey,
    unifySecurityVerify,
    securityLogin,
    queryUserInfo,
    queryLoginHistory,
    switchLoginVerify,
    getVerifyMethod,
    changePassword,
    retrievePassword,
    resetPassword,
    cellphoneVerify,
    switchCellphoneVerify,
    checkVerifyCode,
    emailVerify,
    setAntiFishingCode,
    sendEmailVerifyCode,
    getGoogleSecretKey,
    googleVerify,
    switchGoogleVerify,
    verifyPassword,
    switchEmailVerify,
    retrieveVerify,
}