import Vue from 'vue'
import {getCommodityList,getOTCMarketPrice,getAdsConfigList} from  '@/api/otc'

const otc = {
    state: {
        commodityList:[],
        marketPrice:null,
        adsConfigList:[]
    },
    mutations: {
        SET_COMMODITY_LIST: (state, info) => {
            state.commodityList = info
        },
        SET_MARKETPRICE: (state, marketPrice) => {
            state.marketPrice = marketPrice
        },
        SET_ADS_CONFIG: (state, list) => {
            state.adsConfigList = list
        },
    },
    actions: {
        GetCommodityList:({commit})=>{
            return new Promise((resolve, reject) => {
                getCommodityList({iIdle:0})
                    .then((res)=>{
                        // debugger
                        commit('SET_COMMODITY_LIST',res.data.vCommodity)
                        resolve(res)
                    })
            })
        },
        GetMarketPrice:({commit})=>{
            return new Promise((resolve, reject) => {
                getOTCMarketPrice({sToken:''})
                    .then(res=>{
                        commit('SET_MARKETPRICE',res.data.vList)
                        // debugger
                        resolve(res)
                    })
            })
        },
        GetAdsGrade:({commit})=>{
            return new Promise((resolve, reject) => {
                getAdsConfigList({iIdle:0})
                    .then(res=>{
                        commit('SET_ADS_CONFIG',res.data.vList)
                        resolve(res)
                    })
            })
        },
    }
}

export default otc