const getters = {
    isLogin: state => state.user.isLogin,                                                       //用户登录状态
    email: state => state.user.userInfo.sEmail,                                                 //用户邮箱
    uid: state => state.user.userInfo.iUserId,                                                  //用户UID
    cellphone: state => state.user.userInfo.sCellphone,                                         //用户手机
    sSMSAreaCode: state => state.user.userInfo.sSMSAreaCode,                                    //用户手机国家编码
    lastLoginTime: state => state.user.userInfo.iLastLoginTime,                                 //用户最后登录时间
    lastLoginIPAddr: state => state.user.userInfo.sLastLoginIPAddr,                             //用户最后登录IP
    isHasEmail: state => state.user.userInfo.bIsHasEmail,                                       //是否设置了邮箱
    isHasGoogleSecretKey: state => state.user.userInfo.bIsHasGoogleSecretKey,                   //是否绑定了Google验证器
    switchCellphoneVerify: state => state.user.userInfo.bSwitchCellphoneVerify,                 //手机验证是否开启
    switchEmailVerify: state => state.user.userInfo.bSwitchEmailVerify,                         //邮箱验证是否开启
    switchGoogleVerify: state => state.user.userInfo.bSwitchGoogleVerify,                       //Google验证器是否开启
    userCountry: state => state.user.userCountry,                                               //用户国家区号
    listenSmsStatus: state => state.user.listenSmsStatus,                                       //监听短信状态

    needMfa: state => state.user.needMfa,                                                       //需要安全登录

    iCreditLevel: state => state.user.stUserExtenInfo.iCreditLevel,                             //信用等级
    userName: state => state.user.stUserExtenInfo.sNickName,                                    //用户昵称
    sAvatar: state => state.user.stUserExtenInfo.sAvatar,                                       //用户头像
    userType: state => state.user.stUserExtenInfo.iUserType,                                    //用户类型  普通用户 3，商户 1，承兑商 2
    iCertificateStatus: state => state.user.stUserExtenInfo.iCertificateStatus,                 //用户初级认证状态
    iCertificateId: state => state.user.stUserExtenInfo.iCertificateId,                         //用户初级认证ID
    iLiveCertificateStatus: state => state.user.stUserExtenInfo.iLiveCertificateStatus,         //用户高级认证状态
    iLiveCertificateId: state => state.user.stUserExtenInfo.iLiveCertificateId,                 //用户高级认证ID
    iAdsStatus: state => state.user.stUserExtenInfo.iAdsStatus,                                 //用户广告商认证状态
    iDrawalStatus: state => state.user.stUserExtenInfo.iDrawalStatus,                           //用户是否设置过资金密码
    iMerchantStatus: state => state.user.stUserExtenInfo.iMerchantStatus,                       //用户商户认证状态
    iIsChangeNameStatus: state => state.user.stUserExtenInfo.iIsChangeNameStatus,               //是否修改过昵称
    iTakingOrder: state => state.user.stUserExtenInfo.iTakingOrder,                             //是否接单状态


    platform: state => state.app.platform,                                                      //获取移动端平台信息
    version: state => state.app.version,                                                        //获取移动端版本号
    buildid: state => state.app.buildid,                                                        //获取移动端小版本号

    fiatQuote: state => state.udc.fiatQuote,                                                    //数字货币与法币汇率
    commodityList: state => state.otc.commodityList,                                            //OTC商品列表
    getOrderCount: state => state.user.orderCount,                                              //进行中的订单数量
    getOrderState: state => state.user.orderState,                                              //订单状态
    iDepositAmount: state => state.user.stUserExtenInfo.iDepositAmount,                         //用户商户认证状态
    isWsLogin: state => state.websocket.isWsLogin,                                              //是否登录WS
    getAdsGradeList: state => state.otc.adsConfigList,                                          //获取用户等级
    getAdsGradeVal: (state) => (iLevel) => {                                                    //按照等级获取当个数据
        if(state.otc.adsConfigList){
            const data = state.otc.adsConfigList.find(i=>i.iLevel === iLevel)
            return data||{}
        }
    },
    getMarketdPrice: (state) => (sToken) => {                                                   //按照Token获取OTC市价单价
        if(state.otc.marketPrice){
            const index = state.otc.marketPrice.findIndex(i=>i.sToken === sToken)
            return index > -1 ? Number(state.otc.marketPrice[index].dPrice) : 0
        }
    },
    mailUnreadCount: state => state.user.mailUnreadCount,                                       //获取未读消息数量
    isNetwork: state => state.app.network,                                                      //接口请求错误

}

export default getters
