import Vue from 'vue'
import {getFiatQuote} from  '@/api/udc'

const udc = {
    state: {
        fiatQuote:null
    },
    mutations: {
        SET_FIAT_QUOTE: (state, info) => {
            state.fiatQuote = info
        },
    },
    actions: {
        GetFiatQuote:({commit},sCode)=>{
            return new Promise((resolve, reject) => {
                getFiatQuote({sCode:sCode})
                    .then((res)=>{
                        // debugger
                        commit('SET_FIAT_QUOTE',res.data.vList)
                        resolve(res)
                    })
            })
        },
    }
}

export default udc