import Vue from 'vue'
import store from '@/store'
import {message,notification} from 'ant-design-vue'
import { Dialog,Toast } from 'vant';
import { MSG_TIPS } from "@/utils/msgCode"
const isTest = true
// const isTest = false
const wsCodeList = {
    CmdId_PortalLogin_CS:20001,
    CmdId_PortalLogin_SC:20002,
    Cmd_PortalLogout_CS:20009,
    CmdId_PortalBeatHeart_CS:20003,
    CmdId_PortalBeatHeart_SC:20004,
    CmdId_PortalSubscribe_CS:20005,
    CmdId_PortalSubscribe_SC:20006,
    CmdId_PortalUnubscribe_CS:20007,
    CmdId_PortalUnubscribe_SC:20008,
    CmdId_PortalPush_ForcedExit:20502,                         //强制退出
    CmdId_OTCPush_MarketPrice:27501,
    CmdId_OTCPush_OrderCount:27502,//进行中的订单数
    CmdId_UDCPush_NewMail:30501,
}

let socketClient = null
let beatHeartInterval = null
let lockReconnect = false
let socketIsOpen = false

const websocket = {
    state: {
        webSocket:null,         //websocket实例
        isWsLogin:false         //websocket的登录状态
    },
    mutations: {
        INIT_WEBSOCKET: (state) => {
            if (!socketClient) {
                if(isTest) console.log('建立websocket连接')
                const wsuri = process.env.VUE_APP_WEBSOCKET_URL
                // const wsuri = 'wss://www.bitmex.com/realtimemd'
                socketClient = state.websock = new WebSocket(wsuri)
                state.websock.onmessage = websocketonmessage
                state.websock.onopen = websocketonopen
                state.websock.onerror = websocketonerror
                state.websock.onclose = websocketclose
            } else {
                if(isTest) console.log('websocket已存在')
            }
        },
        LOGIN_WS:()=>{
            loginWs()
        },
        LOGIN_SUCCESS: (state) =>{
            state.isWsLogin = true
        },
        LOGOUT_WS:(state)=>{
            state.isWsLogin = false
        },
        RECONNECT_WS:()=>{
            if(socketIsOpen){
                sendMessage('Cmd_PortalLogout_CS',{iIdle:0})
            }
        }
    },
    actions: {

    }
}

function websocketonopen () {
    console.log('websocket已连接')
    socketIsOpen = true

    if(store.getters.isLogin && !store.getters.isWsLogin)loginWs()
    //订阅市价
    sendMessage('CmdId_PortalSubscribe_CS',{vSubject:['GP.MarketPrice']})
    // 连接之后创建心跳
    setBeatHeartInterval()
}

function websocketonerror (e) {
    // 连接建立失败关闭重连
    console.log('链接错误:', e)
    socketClient.close()
}

function websocketonmessage (e) { // 数据接收
    // debugger
    dataParser(JSON.parse(e.data))
}

function websocketclose (e) { // 关闭
    console.log('断开连接', e)
    socketIsOpen = false
    clearInterval(beatHeartInterval)
    socketClient = null
    store.commit('LOGOUT_WS')
    reconnect()
}

function reconnect (){
    if (lockReconnect) return
    lockReconnect = true
    setTimeout(function () { // 没连接上会一直重连，设置延迟避免请求过多
        store.commit('INIT_WEBSOCKET')
        lockReconnect = false
    }, 500)
}

function setBeatHeartInterval(){
    console.log('setBeatHeartInterval')
    beatHeartInterval = setInterval(()=>{
        // debugger
        //判断是否登录系统,登录了则需要执行ws的登录
        if(store.getters.isLogin && !store.getters.isWsLogin)loginWs()
        sendMessage('CmdId_PortalBeatHeart_CS',{iIdle:0})
    },3*1000)
}

function loginWs () {
    if(socketIsOpen){
        const params = {
            iUserId:store.getters.uid,
            sSessionKey:Vue.ls.get('SSK'),
            sPlatform:'app'
        }
        sendMessage('CmdId_PortalLogin_CS',params)
    }
}

function sendMessage (idName,params) {
    const op = {
        iCmdId:wsCodeList[idName],
        iCmdSeq:0,
        sData: JSON.stringify(params)
    }
    if(isTest && idName !=='CmdId_PortalBeatHeart_CS')console.log(idName,params)
    socketClient.send(JSON.stringify(op))
}

function dataParser(data){
    const whiteList = [0,-509]   //状态码白名单，不使用统一的状态码错误处理，让每个请求自身处理
    if(data.iRspCode === -509 && data.iCmdId !== 20002){
        loginWs ()
    }
    if(!whiteList.includes(data.iRspCode)){
        Toast.fail(MSG_TIPS(store.state.user.errorMsg,data.iRspCode))
        return
    }
    
    // console.log(data.iCmdId,'data.iCmdId',data)
    store.commit('SET_ORDER_STATE',false)
    switch (data.iCmdId){
        case 20002:
            if(data.iRspCode === -509 )return
            store.commit('LOGIN_SUCCESS')
            if(isTest)console.log('LOGIN_SUCCESS:')
            break
        case 20004:
            if(isTest)console.log('pong')
            break
        case 20006:
            if(isTest)console.log('订阅成功！')
            break
        case 27501:
            store.commit('SET_MARKETPRICE',data.stData.vList)
            // debugger
            break
        case 27502:
            store.commit('SET_ORDER_COUNT',data.stData.iOrderCount)
            break
            
        case 30501: // 未读消息
            //store.dispatch('GetMailUnreadCount') 电脑pc端
            // debugger
            console.log('推小星星推小星星推小星星推小星星推小星星')
            store.commit('SET_ORDER_STATE',true)
            switch (data.stData.iType){
                case 40:
                    notification.info({
                        message:'您有新的订单！',
                        description:'请至订单中心查看详情'
                    })
                    break
                case 41:
                    notification.success({
                        message:'买家支付通知！',
                        description:'您有一个订单买家已转账支付，请尽快确认收款并至订单中心进行放币操作！'
                    })
                    break
                case 42:
                    notification.error({
                        message:'订单取消！',
                        description:'您有一个订单买家已取消订单，请至订单中心查看详情！'
                    })
                    break
                case 43:
                    notification.success({
                        message:'放币通知！',
                        description:'您有一个订单卖家已放币，订单已完成，请至订单中心查看详情！'
                    })
                    break
                case 45:
                    notification.error({
                        message:'放币超时！',
                        description:'您有一个订单放币已超时，请尽快至订单中心处理，否则将影响您的信用评级！'
                    })
                    break
                case 46:
                    notification.error({
                        message:'订单取消！',
                        description:'您有一个订单支付超时，已自动取消。请至订单中心查看详情,若您已转款，请联系客服！'
                    })
                    break
                case 47:
                    notification.error({
                        message:'订单取消！',
                        description:'您有一个订单买家支付超时，已自动取消。请至订单中心查看详情'
                    })
                    break
            }
            break
        case 20502:
            Dialog.alert({
                title: '退出登录',
                messageAlign: 'left',
                message: '系统检测到您的账号在别的地方登录，若不是您亲自操作，则您的密码可能泄露，请尽快至安全中心进行相关操作，以免造成您的资产损失',
                confirmButtonText:'我知道了'
            })
            store.dispatch('Logout')
            break
        default:
            if(isTest)console.log('data:',data)
    }
}
//心跳检测
let heartCheck = {
    timeout: 540000,        //9分钟发一次心跳
    timeoutObj: null,
    serverTimeoutObj: null,
    reset: function(){
        clearTimeout(this.timeoutObj);
        clearTimeout(this.serverTimeoutObj);
        return this;
    },
    start: function(){
        let self = this;
        this.timeoutObj = setTimeout(function(){
            //这里发送一个心跳，后端收到后，返回一个心跳消息，
            //onmessage拿到返回的心跳就说明连接正常
            ws.send("ping");
            console.log("ping!----")
            self.serverTimeoutObj = setTimeout(function(){//如果超过一定时间还没重置，说明后端主动断开了
                ws.close();     //如果onclose会执行reconnect，我们执行ws.close()就行了.如果直接执行reconnect 会触发onclose导致重连两次
            }, self.timeout)
        }, this.timeout)
    }
}

export default websocket