const verifyDrawalPassword = (params)=>postAction(udcUrl.VerifyDrawalPassword,params);              //验证充提币密码
const updateUserLanguage = (params)=>postAction(udcUrl.UpdateUserLanguage,params);                  //更新用户语言
const updateUserLegalCurrency = (params)=>postAction(udcUrl.UpdateUserLegalCurrency,params);        //更新用户法币
const addCertificate = (params)=>postAction(udcUrl.AddCertificate,params);                          //初级身份验证
const getCertificate = (params)=>postAction(udcUrl.GetCertificate,params);                          //获取初级身份验证
const addLiveCertificate = (params)=>postAction(udcUrl.AddLiveCertificate,params);                  //高级身份验证
const applyAdsExtCertificate = (params)=>postAction(udcUrl.ApplyAdsExtCertificate,params);          //高级身份验证
const getLiveCertificate = (params)=>postAction(udcUrl.GetLiveCertificate,params);                  //获取高级身份验证
const addAccountAddress = (params)=>postAction(udcUrl.AddAccountAddress,params);                    //添加提币地址
const delAccountAddress = (params)=>postAction(udcUrl.DelAccountAddress,params);                    //删除提币地址
const getAccountAddress = (params)=>postAction(udcUrl.GetAccountAddress,params);                    //获取提币地址
const getUserBalance = (params)=>postAction(udcUrl.GetUserBalance,params);                          //获取账户余额
