const applyMerchantCertificate = (params)=>postAction(udcUrl.ApplyMerchantCertificate,params);      //申请商户认证
const getMerchantCertificate = (params)=>postAction(udcUrl.GetMerchantCertificate,params);          //获取商户认证信息
const getAppVersion = (params)=>postAction(udcUrl.GetAppVersion,params);                            //获取APP版本号
const queryCountry = (params)=>postAction(udcUrl.QueryCountry,params);                              //获取国家列表
const sendUserMail = (params)=>postAction(udcUrl.SendUserMail,params);                              //用户发送消息
const getUserMailUnreadCount = (params)=>postAction(udcUrl.GetUserMailUnreadCount,params);          //获取未读消息数量
const getUserMailList = (params)=>postAction(udcUrl.GetUserMailList,params);                        //获取用户消息列表
const readUserMail = (params)=>postAction(udcUrl.ReadUserMail,params);                              //标记消息为已读
const readUserAllMail = (params)=>postAction(udcUrl.ReadUserAllMail,params);                        //一键已读
const getCustomServant = (params)=>postAction(udcUrl.GetCustomServant,params);                      //获取客服列表
const getQueryCountry = (params)=>postAction(udcUrl.QueryCountry,params);                           //查询用户国家
const getFiatQuote = (params)=>postAction(udcUrl.GetFiatQuote,params);                              //获取数字货币与法币汇率
const setMerchantCallback = (params)=>postAction(udcUrl.SetMerchantCallback,params);                //获取数字货币与法币汇率
const getErrorCode = (params)=>postAction(udcUrl.ErrorCode,params);                                 //获取错误码
const userTakeingOrder = (params)=>postAction(udcUrl.UserTakeingOrder,params);                      //开启关闭接单状态
const getUserJournalList = (params)=>postAction(udcUrl.GetUserJournalList,params);                  //财务记录
const queryFinanceList = (params)=>postAction(udcUrl.QueryFinanceList,params);                      //获取充提记录

