    editAccountAddress,
    changeApiKey,
    getApiKeyList,
    delApiKey,
    applyApiKey,
    getPlatformMessage,
    registerUserExten,
    getUserExten,
    chgUserExten,
    resetDrawalPassword,
    verifyDrawalPassword,
    updateUserLanguage,
    updateUserLegalCurrency,
    addCertificate,
    getCertificate,
    addLiveCertificate,
    getLiveCertificate,
    addAccountAddress,
    delAccountAddress,
    getAccountAddress,
    getUserBalance,
    applyAdsCertificate,
    getAdsCertificate,
    getAdsExtCertificate,
    applyMerchantCertificate,
    applyAdsExtCertificate,
    getMerchantCertificate,
    getAppVersion,
    queryCountry,
    sendUserMail,
    getUserMailUnreadCount,
    getUserMailList,
    readUserMail,
    readUserAllMail,
    getCustomServant,
    getQueryCountry,
    getFiatQuote,
    setMerchantCallback,
    getErrorCode,
    userTakeingOrder,
    queryFinanceList,
