/**
 * 邮箱
 * @param {*} s
 */
export function isEmail (s) {
  return /^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(s)
}

/**
 * 手机号码
 * @param {*} s
 */
export function isMobile (s) {
  return /^1[0-9]{10}$/.test(s)
}

/**
 * 电话号码
 * @param {*} s
 */
export function isPhone (s) {
  return /^([0-9]{3,4}-)?[0-9]{7,8}$/.test(s)
}

/**
 * URL地址
 * @param {*} s
 */
export function isURL (s) {
  return /^http[s]?:\/\/.*/.test(s)
}
/**
 * 密码规则
 * @param {*} str 
 */
export function regPwd(str){
  // 密码需包含数字和大小写字母中至少两种字符的8-10位字符
  const regex = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)[0-9A-Za-z]{8,10}$/;
  if (!regex.test(str)) {
    return false
  } else {
    return true
  }
}
// 身份证规则
export function identityCodeValid(str) {
  const regex = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/;
  if (!regex.test(str)) {
    return false
  } else {
    return true
  }
}

/**
 * 计算精度(乘法)
 */
export function accMul (num1,num2) {
  let m = 0,
      n1 = num1.toString(),
      n2 = num2.toString();
	try {
	    m += n1.split(".")[1].length
	} catch (e) {}
	try {
		  m += n2.split(".")[1].length
	} catch (e) {}
	return Number(n1.replace(".", "")) * Number(n2.replace(".", "")) / Math.pow(10, m)
}

/**
 * 计算精度(除法)
 */

//说明：javascript的除法结果会有误差，在两个浮点数相除的时候会比较明显。这个函数返回较为精确的除法结果。
//调用：accDiv(arg1,arg2)
//返回值：arg1除以arg2的精确结果
export function accDiv (num1,num2){
  console.log(num1,num2,'num1,num2')
  let n1 = 0,
    n2 = 0,
    r1, r2;
  try {
    n1 = num1.toString().split(".")[1].length
  } catch (e) {}
  try {
    n2 = num2.toString().split(".")[1].length
  } catch (e) {}
  
  r1 = Number(num1.toString().replace(".", ""))
  r2 = Number(num2.toString().replace(".", ""))
  return (r1 / r2) * Math.pow(10, n2 - n1);
}