import Vue from 'vue'
import axios from 'axios'
import dayjs from 'dayjs'
import {authUrl} from '@/api/auth'
import store from '@/store'
import { MSG_TIPS } from "./msgCode"
import {Toast, Notify} from 'vant';

// console.log('API_URL:',process.env.VUE_APP_API_URL)
const service = axios.create({
    // baseURL:process.env.VUE_APP_API_URL, // api base_url
    timeout: 20000 // 请求超时时间
})

service.defaults.withCredentials = false // 请求带COOKIE
service.defaults.headers['Content-Type'] = 'application/json;charset=UTF-8'

//错误处理
const err = (error) => {
    if(window.networkFlag == 2) return Notify({ type: 'warning', message: '当前无网络，请检查网络连接' });
    //Todo 统一的请求错误处理
    if (error && error.stack.indexOf('timeout') > -1) {
        // Toast('请求超时')
        return
    }
    return Promise.reject(error)
}

// request interceptor
service.interceptors.request.use(async config => {
    //if(window.networkFlag ==2) return Notify({ type: 'warning', message: '当前无网络，请检查网络连接' });
    //获取sSessionKey
    const ssk = Vue.ls.get('SSK')
    if(ssk){
        const now = dayjs().valueOf()
        let keyString = decodeURIComponent(escape(window.atob(ssk.split('.')[1])))
        let key = JSON.parse(keyString)

        // 当前时间离过期时间不足一小时，则刷新sSessionKey,否则请求头统一加上Authorization
        if(key.exp * 1000 - now > 0 && key.exp * 1000 - now <=  60 * 60 * 1000) {
            try{
                const res =await  axios({
                    url: process.env.VUE_APP_API_URL + authUrl.RefreshSessionKey,
                    method:'post' ,
                    data: {iSource:1},
                    headers:{
                        'Content-Type':'application/json;charset=UTF-8',
                        Authorization:ssk
                    }
                })
                if(res.data.iRspCode === 0){
                    let newKey = res.data.data.sSessionKey
                    let keyString = decodeURIComponent(escape(window.atob(newKey.split('.')[1])))
                    let key = JSON.parse(keyString)
                    Vue.ls.set('SSK',newKey,key.exp*1000 - dayjs().valueOf())

                    //请求使用刷新后的Key作为Authorization 验证头
                    config.headers.common.Authorization = newKey
                }else{
                    //debugger
                    Toast.fail('请求错误:' + res.data.iRspCode)
                }
            }catch(e){
                // debugger
                console.log(e)
            }
        }else{
            // 请求统一带上 Authorization 验证头
            config.headers.common.Authorization = ssk
        }
    }
    // config.headers.
    return config
}, err)

// response interceptor
service.interceptors.response.use((response) => {
    const resCode = response.data.iRspCode
    const resData = response.data
    //Todo 这里匹配状态码
    const whiteList = [0,-1106,-509,-1134]   //状态码白名单，不使用统一的状态码错误处理，让每个请求自身处理
    //-509 无权限访问，则执行退出登录方法
    if(resCode === -509){
        Toast.fail('请登录！')
        store.dispatch('Logout')
        return Promise.reject()

    }
    if(resCode === -1134){
        return Promise.resolve(response.data)
    }

    if(whiteList.includes(resCode)){
        return resData
    }else{
        // debugger
        let arr = store.state.user.errorMsg
        Toast.fail(MSG_TIPS(arr,resCode))
        return Promise.reject({
            requestError:true,
            message:'请求错误',
            code:resCode
        })
    }
}, err)

export {
    service as axios
}
